/* INTRO 2023-12-11: 引入 http_req 用于创建请求 */
import http_req from "@/utils/http-req"

export default {

  get_courses(tid){
    console.log(tid)
    return http_req({
      url:"/teacher/teacherCourse",
      method: "get",
      params: tid
    })
  },

  get_courses_detail(course_id){
    console.log(course_id)
    return http_req({
      url:"/teacher/courseDetail",
      method: "get",
      params: course_id.course_id
    })
  },

  get_applications(tid){
    return http_req({
      url:"/teacher/teacherApplication",
      method: "get",
      params: tid
    })
  },

  get_application_detail(application_id){
    return http_req({
      url:"/teacher/applicationDetail",
      method: "get",
      params: application_id.application_id
    })
  },

  apply_for_course(course_application_DTO){
    return http_req({
      url:"/teacher/applyCourse",
      method: "post",
      data: course_application_DTO
    })
  },

  /* INTRO 2023-12-11: 定义与后端对应的api */
  giveScoreForm(student_course_DTO/* :{course_id:String} */ ) {
  return http_req({
    url: "/teacher/giveScoreForm",
    method: "get",
    params:student_course_DTO
  })
  },

  getStudentScore(student_course_DTO){
    return http_req({
      url: "/teacher/giveScoreForm",
      method: "get",
      params:student_course_DTO
    })
  },


  giveScore(student_course_DTO){
    return http_req({
      url: "/teacher/giveScore",
      method: "post",
      data:student_course_DTO
    })
  },

  scoreAnalyse(student_course_DTO){
    return http_req({
      url: "/teacher/scoreAnalyse",
      method: "get",
      params:student_course_DTO
    })

  },

  getCourseName(student_course_DTO){
    return http_req({
    url: "/teacher/getCourseName",
    method: "get",
    params:student_course_DTO
  })

  },

  ice_bee() {
    return http_req({
      url: "/icebee",
      method: "get"
    })
  }
}
