<template>
  <a-result style="width: 60vw; margin-top: 10vh;" v-if="success_end"
  status="success" title="This is title content" >
    <template #subtitle>
      申请成功！
    </template>
    <template #extra>
      <a-space>
        <a-button @click="success" type='primary'>返回</a-button>
      </a-space>
    </template>
  </a-result>

  <a-form :model="form" @submit="add_course" style="width: 60%; margin-top: 5%; margin-left: 10%;"
    v-if="!success_end">

    <!-- 标题 -->
    <a-form-item>
      <h1 style="font-size: 25px;">开课申请</h1>
    </a-form-item>

    <!-- 课程名 -->
    <a-form-item label="课程名" :rules="[{ required: true, message: '请输入课程名' }]">
      <a-input v-model="course_name" />
    </a-form-item>

    <!-- 学分 -->
    <a-form-item field="credit" label="学分">
      <a-input-number v-model="credit" :min="0.5" :precision="0.1" :step="0.5" />
    </a-form-item>

    <!-- 课程安排 -->
    <a-form-item field="arrangement" label="课程安排">
      <a-date-picker show-confirm-btn hide-trigger @ok="onDateConf" @select="onDateSelect" style="width: 350px;">
        <template #cell="{ date }">
          <div class="arco-picker-date">
            <div class="arco-picker-date-value" :style="getCellStyle(date)">
              {{ date.getDate() }}
            </div>
          </div>
        </template>
        <template #extra>
          <a-checkbox-group v-model="currentBatch">
            <a-checkbox value="1">第一节</a-checkbox>
            <a-input v-model="currentClassRoom[1]"
             :disabled=disbaleClassRoomInput(1) 
             type="text" 
             style="width: 180px;">
              <template #prefix>
                教室
              </template>
            </a-input>
            <a-checkbox value="2">第二节</a-checkbox>
            <a-input v-model="currentClassRoom[2]" :disabled=disbaleClassRoomInput(2) type="text" style="width: 180px;">
              <template #prefix>
                教室
              </template>
            </a-input>
            <a-checkbox value="3">第三节</a-checkbox>
            <a-input v-model="currentClassRoom[3]" :disabled=disbaleClassRoomInput(3) type="text" style="width: 180px;">
              <template #prefix>
                教室
              </template>
            </a-input>
            <a-checkbox value="4">第四节</a-checkbox>
            <a-input v-model="currentClassRoom[4]" :disabled=disbaleClassRoomInput(4) type="text" style="width: 180px;">
              <template #prefix>
                教室
              </template>
            </a-input>
            <a-checkbox value="5">第五节</a-checkbox>
            <a-input v-model="currentClassRoom[5]" :disabled=disbaleClassRoomInput(5) type="text" style="width: 180px;">
              <template #prefix>
                教室
              </template>
            </a-input>
            <a-checkbox value="6">第六节</a-checkbox>
            <a-input v-model="currentClassRoom[6]" :disabled=disbaleClassRoomInput(6) type="text" style="width: 180px;">
              <template #prefix>
                教室
              </template>
            </a-input>
          </a-checkbox-group>
          
        </template>
      </a-date-picker>
    </a-form-item>

    <!-- 可选人数 -->
    <a-form-item label="可选人数" :rules="[{ required: true, message: '请输入可选人数' }]">
      <a-input-number v-model="max_student" :min="1"  :step="1" :max="120" />
    </a-form-item>

    <!-- 授课教师 -->
    <!-- <a-form-item field="teacher" label="授课教师名字" :rules="[{ required: true, message: '请输入授课教师名字' }]">
      <a-input v-model="form.teacher" />
    </a-form-item> -->

    <!-- 按钮 -->
    <a-form-item>
      <a-space>
        <template #split><a-divider direction="vertical" /></template>
        <a-button type="primary" html-type="submit" :loading="add_course_btn_loading">
          申请开课
        </a-button>
        <a-button @click="clear_form">
          清空输入
        </a-button>
      </a-space>
    </a-form-item>
  </a-form>
</template>
  
<script>
import teacher_api from "@/api/teacher/teacher-api"
import auth_api from "@/api/auth-api"
export default {
  data() {
    return {
      course_name: "",
      credit:0,

      currentBatch: [],
      selectedDates: [],
      currentClassRoom: [],
      arrangement: [],
      
      max_student: 1,
      success_end: false,
      add_course_btn_loading: false,
      highlightStyle: {
        border: '1px solid rgb(var(--arcoblue-6))',
      },
    }
  },
  methods: {
    disbaleClassRoomInput(value){
      var ans = true
      return !ans;
    },

    success(){
      this.success_end=false
    },

    onDateConf(valueString, value) {

      if(!this.checkClassRoom()){
        return;
      }
      if (this.selectedDates.includes(valueString)) {

        // 取消选择
        if (this.currentBatch.length == 0) {
          this.selectedDates.splice(this.selectedDates.indexOf(valueString), 1)
          this.arrangement = this.arrangement.filter(data => data.date != valueString)
        } 

        // 修改选择
        else {
          this.arrangement = this.arrangement.filter(data => data.date != valueString)
          
          for (let index = 1; index <= 6; index++) {
            if(this.currentBatch.includes(String(index))){
              this.arrangement.push({date: valueString, batch: index, class_room: this.currentClassRoom[index]})
            }
            
          }
        }
      }

      //新增选择
      else if (this.currentBatch.length > 0) {
        this.selectedDates.push(valueString)
        for (let index = 1; index <= 6; index++) {
          if(this.currentBatch.includes(String(index))){
            this.arrangement.push({date: valueString, batch: index, class_room: this.currentClassRoom[index]})
          }
        }
      }
      else {

      }

      // console.log(this.currentClassRoom)
      console.log(this.arrangement)
    },

    onDateSelect(valueString, value) {
      if (this.selectedDates.includes(valueString)) {
        // console.log(this.arrangement.find(data => data.date === valueString))
        this.currentBatch=[]
        this.arrangement.filter(data => data.date === valueString).forEach(a=>{
          this.currentBatch.push(String(a.batch))
        })

        var datess = this.arrangement.filter(a=> a.date === valueString)

        for (let index = 1; index <= 6; index++) {
          if(this.currentBatch.includes(String(index))){
            this.currentClassRoom[index] = datess.find(a=> a.batch == index).class_room
          }
          else{
            this.currentClassRoom[index]=""
          }
        }
      }
      else {
        this.currentBatch = [];
      }
    },

    getCellStyle(date) {
      var y = date.getFullYear()
      var m = date.getMonth() + 1
      m = m < 10 ? '0' + m : m
      var d = date.getDate()
      d = d < 10 ? '0' + d : d
      var fd = y + '-' + m + '-' + d

      return this.selectedDates.includes(fd) ? this.highlightStyle : {}
    },

    disabledDate(current) {
      const dates = this.dates;
      if (dates && dates.length) {
        const tooLate = dates[0] && Math.abs((new Date(current) - dates[0]) / (24 * 60 * 60 * 1000)) > 7;
        const tooEarly = dates[1] && Math.abs((new Date(current) - dates[1]) / (24 * 60 * 60 * 1000)) > 7;
        return tooEarly || tooLate;
      }
      return false;
    },

    checkClassRoom(){
      for (let index = 1; index <= 6; index++) {
        if(this.currentBatch.includes(String(index))){
          if(this.currentClassRoom[index] ==undefined){
            // console.log(this.currentBatch.includes(String(index)))
            this.$message.warning("请填写教室信息")
            return false
          }
        }
      }
      return true;
    },

    clear_form() {
      console.log("clear")
      this.course_name= ""
      this.credit=""
      this.currentBatch= []
      this.selectedDates= []
      this.arrangement= []
      this.max_student= ""
    },

    is_form_all_not_null() {
      return (this.course_name != ""
        && this.credit != ""
        && this.max_student != ""
        // && this.selectedDates != ""
        )
    },

    set_is_add_course_btn_loadin(is_loading) {
      this.add_course_btn_loading = is_loading
    },

    add_course({ values, errors }) {
      if (!this.is_form_all_not_null()) {
        this.$message.warning("请填写信息")
        return
      }
      this.set_is_add_course_btn_loadin(true)

      var fin_data={
        // proposer_id: sessionStorage.getItem("user_id"),
        proposer_id: auth_api.getcookie("user_id"),
        course_name: this.course_name,
        max_student: this.max_student,
        credit: this.credit,
        arrangement: this.arrangement
      }

      console.log(fin_data)

      teacher_api.apply_for_course(fin_data)
        .then(resp => {
          this.set_is_add_course_btn_loadin(false)
          if (resp.status_code != 200) {
          
            this.$message.error("申请失败，" + resp.data)
          }else {
            this.success_end=true
            this.clear_form()
            console.log(resp)
            this.$message.info("申请成功")
          }
        })
        .catch(err => {
          this.set_is_add_course_btn_loadin(false)

          console.error(err)
          this.$message.error("申请失败，出现异常")
        })
    }
  }
}
</script>
  
<style scoped></style>